// es6  promise api 
export default (params) => {
	// 
	uni.showLoading({
		title: ''
	})

	return new Promise((resolve, reject) => {
		wx.request({
			...params,
			success(res) {
				resolve(res)
			},
			fail(err) {
				reject(err)
			},
			complete() {
				uni.hideLoading();
			}
		})
	})
}
